import React from 'react';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Layout from 'layout/Layout';
import Metadata from 'components/Metadata';
import { ErrorPageMetadata } from 'configs/PageMetadata';
import { Images } from 'configs/SiteConfig';

const useStyles = makeStyles(() => ({
  root: {
    margin: 20,
    padding: 20
  },
  img: {
    maxWidth: "560px",
    width: "100%"
  }
}));

export default function AboutPage(data) {
  const classes = useStyles();
  const browser = typeof window !== "undefined" && window;
  return (
    browser && (
      <Layout>
        <Paper elevation={3} className={classes.root} >        
          <Typography variant="h6">
            That's an error â˜¹! We cound't find the requested page on this website.
          </Typography>
          <br/><br/>
          <img src={Images.Error} className={classes.img} alt="Error"></img>
        </Paper>
        <Metadata
          Title={ErrorPageMetadata.Title}
          Description={ErrorPageMetadata.Description}
        />
      </Layout>
    )

  );
}